@import "./general.sass"

.header
  display: flex
  justify-content: space-between
  align-items: center
  width: 100%

  &__logo
    width: 145px
    height: 100%

  .menu
    display: flex
    gap: 20px
    margin-right: 10px

    a
      font-size: 12px

    .active
      text-decoration: underline 
      
@media screen and (min-width: $breakpoint-mobile)
  .header

    &__logo
      width: 210px

    .menu
      display: flex
      gap: 40px

      a
        font-size: 24px 



